html {
  -ms-touch-action: manipulation;
      touch-action: manipulation;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  overflow-x: hidden;

  height: 100%;
}

body {
  height: 100%;
  margin: 0;
  background-color:white;
}

h1,h2,h3,h4,h5,h6 {
  margin-bottom: 15px;
}
h1 {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-family: 'Raleway', sans-serif;
  font-weight: 100;
  font-size: 5vw;
  color: white;
}

h2 {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-family: 'Raleway', sans-serif;
  font-size: 36px;
  font-weight: 400;
  color: black;
  text-align: left;
}

h3 {
  font-family: 'Raleway', sans-serif;
  font-size: 28px;
  font-weight: 400;
  color: black;
  text-align: left;
}

h4,h5,h6 {
  font-family: 'Raleway', sans-serif;
  font-size: 16px;
  font-weight: 200;
  color: white;
}

p {
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  font-weight: 200;
  color: rgb(50, 50, 50);
}

a {
  text-decoration: none;
}

